body {
  overflow-x: hidden;
}

.container {
  background-color: #f5f5f5;
  width: 100vw;
  min-height: 100vh;
}

.cv {
  min-height: 100vh;
  padding: 2rem 7rem;
  display: flex;
  flex-direction: row-reverse;
  align-items: stretch;
  position: relative;
}

.ribbon {
  flex: 0 0 25%;
  background-color: #198DA5;
  display: flex;
  flex-direction: column;
  position: relative;
  -webkit-print-color-adjust: exact;
}

.ribbon-section {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.ribbon-section.centered {
  align-items: center;
}

.ribbon-section > h3 {
  color: #fff;
  font-weight: 700;
  margin: 0 1rem;
}

.profile-pic {
  margin: 2rem 0;
  height: 10rem;
  width: auto;
  border-radius: 30%;
}

.profile-name {
  color: #fff;
  font-size: 1.7rem;
  font-weight: 900;
  text-align: center;
  margin: 0;
}

.profile-title {
  color: #d3d3d3;
  font-size: 1.5rem;
  text-align: center;
  margin: 1rem;
}

.section-list {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
}

.contact-item {
  margin: 1rem;
  color: #fff;
}

.circle {
  display: inline-block;
  background-color: #2d7788;
  border-radius: 50%;
  min-width: 20px;
  min-height: 20px;
  padding: 5px;
  text-align: center;
  line-height: 1;
  box-sizing: content-box;
  white-space: nowrap;
  -webkit-print-color-adjust: exact;
}

.circle::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  padding-top: 100%;
  height: 0;
}

.circle > * {
  display: inline-block;
  vertical-align: middle;
}

.contact-item > .item-image > i {
  color: #fff;
}

.contact-text {
  font-size: 0.8rem;
  margin-left: 0.5rem;
  text-decoration: none;
  color: #fff;
}

.education-item
{
  margin-left: 1.5rem;
  margin-top: 1rem;
}

.language-item {
  margin-left: 1.5rem;
  margin-top: 0.5rem;
}

.language-item:last-child {
  margin-top: 0.2rem;
}

.education-item > *,
.language-item > * {
  margin: 0.5rem 0;
  color: #fff;
}

.language-item > * {
  display: inline-block;
}

.education-item > h6,
.language-item > h6 {
  opacity: .6;
  font-size: .75rem;
}

.language-item > h6 {
  margin-left: 0.5rem;
}

.cv-body {
  background-color: #fff;
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 1.5rem;
}

.body-section-title {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.body-section-title > .icon {
  color: #fff;
  font-size: 1rem;
}

.body-section-title > h2 {
  text-transform: uppercase;
  color: #2d7788;
  font-weight: 500;
  margin-left: 1rem;
}

.body-section .about-me {
  color: #545e6c;
  line-height: 1.5;
  text-align: justify;
  padding: 0 0.5rem;
}

.experience-list {
  display: flex;
  flex-direction: column;
}

.experience-item:hover {
  background: rgba(0,0,0,.2);
}

.experience-item {
  display: grid;
  grid-template-areas:
  "position . date"
  "company . ."
  "description description description";
  padding: 0.5rem;
}

.experience-position {
  grid-area: position;
  color: #3f4650;
}

.experience-date {
  grid-area: date;
  color: #97aac3;
  text-align: right;
}

.experience-company {
  grid-area: company;
  color: #97aac3;
}

.experience-description {
  grid-area: description;
  color: #545e6c;
  line-height: 1.5;
  text-align: justify;
}

.skill-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 0 2rem;
}

.skill-list-item {
  flex: 1 0 45%;
  margin: 0.5rem 0;
  align-items: center;
  margin-right: 1rem;
  max-width: 50%;
}

.skill-list-item > * {
  display: inline;
  color: #2d7788;
  -webkit-print-color-adjust: exact;
}

.skill-bar {
  position: relative;
  background-color: #f5f5f5;
  border-radius: 25px;
  height: 1rem;
  -webkit-print-color-adjust: exact;
  width: 100%;
}

.skill-bar-full-fill {
  width: 100%;
  position: relative;
  height: inherit;
  background-color: #f5f5f5;
  text-align: center;
  line-height: 1rem;
  color: #fff;
  border-radius: inherit;
  -webkit-print-color-adjust: exact;
}

.skill-bar-fill {
  position: relative;
  height: inherit;
  background-color: #7bc2d3;
  text-align: center;
  line-height: 1rem;
  color: #fff;
  border-radius: inherit;
  -webkit-print-color-adjust: exact;
}

.skill-bar-text {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0;
}

.fab {
  position: fixed;
  bottom: 30px;
  right: 15px;
  width: 3.75rem;
  height: 3.75rem;
  z-index: 999;
  cursor: pointer;
  border-radius: 100%;
  background-color: #2d7788;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 2rem;
}

@media print {

  body {
    font-size: 0.8rem;
  }

  .cv {
    padding: 0;
  }

  .profile-pic {
    margin: 1rem 0;
  }

  .profile-name {
    font-size: 1.5rem;
  }

  .profile-title {
    font-size: 1rem;
  }

  .about-me > p {
    margin-top: 0.25rem;
    margin-bottom: 0.25rem;
  }

  .body-section-title > h2 {
    margin-left: 0.5rem;
  }

  .body-section-title > h2 {
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
  }
  .experience-description {
    margin-top: 0.25rem;
    margin-bottom: 0.25rem;
  }

  .experience-position {
    color: #7bc2d3;
  }

  .contact-item {
    margin: 0 0.25rem 0.25rem 0.75rem;
  }

  .contact-text {
    font-size: 0.75rem;
    margin-left: 0.25rem;
  }

  .ribbon-section {
    margin-top: 1rem;
  }

  .skill-list-item {
    max-width: 80%;
  }

  .fab {
    display: none;
  }
}

@media only screen and (max-width: 500px) {
  .cv {
    padding: 0;
  }

  .section-list {
    flex-direction: column;
  }

  .section-list > li {
    margin: 0;
  }
}

@media only screen and (max-width: 1090px) {
  .skill-list {
    grid-template-columns: 1fr;
  }
}

@media only screen and (max-width: 750px) {
  .cv {
    flex-direction: column;
  }

  .ribbon-section {
    margin: 1rem 0;
  }

  .ribbon-section > h3 {
    text-align: center;
  }

  .section-list {
    width: auto;
  }

  .section-list:not(:first-child) {
    display: flex;
    text-align: center;
  }

  .section-list:not(:first-child) > li {
    flex: 1;
  }

  .skill-list-item {
    max-width: initial;
  }
}